/* src/pages/Warehouse.css */
.warehouse {
    padding: 20px;
  }
  
  .filter-input {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .add-category-btn, .add-ingredient-btn {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 10px 20px;
    margin-bottom: 20px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .category {
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .category-header {
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: #f0f0f0;
    cursor: pointer;
  }
  
  .category-header h2 {
    margin-left: 10px;
  }
  
  .category-content {
    padding: 10px;
  }
  
  .ingredients-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .ingredients-table th, .ingredients-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .ingredients-table tr:hover {
    background-color: #f5f5f5;
    cursor: pointer;
  }