.recipes-container {
  padding: 20px;
}

.recipes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}

.recipe-card {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  overflow: hidden;
  transition: transform 0.2s;
  cursor: pointer;
}

.recipe-card:hover {
  transform: translateY(-5px);
}

.recipe-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.recipe-title {
  padding: 15px;
  margin: 0;
  font-size: 1.1em;
  text-align: center;
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
}
.recipe-image-container {
  position: relative;
}

.delete-button {
  position: relative;
  top: 10px;
  right: 10px;
  left: 10px;
  background-color: rgba(255, 0, 0, 0.7);
  color: white;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s;
}

.delete-button:hover {
  background-color: rgba(255, 0, 0, 0.9);
}

.delete-button:focus {
  outline: none;
}