* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  max-width: 100%;
  padding: 15px;
}

/* Стили слайдера */
.slider-container {
  position: relative;
  width: 100%;
  margin-bottom: 20px;
}

.slider {
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
  border-radius: 10px;
}

.slider-wrapper {
  display: flex;
  transition: transform 0.3s ease;
}

.slider-wrapper img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.default-slide {
  width: 100%;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f0f0f0;
}

.slider-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0,0,0,0.5);
  border: none;
  color: white;
  padding: 10px;
  cursor: pointer;
}

.slider-btn.prev { left: 10px; }
.slider-btn.next { right: 10px; }

.slider-controls {
  position: absolute;
  bottom: 10px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}

/* Стили для блока ингредиентов */
.ingredients-container {
  margin-bottom: 20px;
}

.ingredients-header {
  display: flex;
  padding: 2px;
  background: #f0f0f0;
  margin-bottom: 10px;
}

.ingredient-name { width: 50%; }
.ingredient-weight,
.ingredient-waste { 
  display: block;
  left: 10px;
  width: 10%; }

.ingredient-row {
  display: flex;
  padding: 2px 10px;
  border-bottom: 1px solid #ddd;
}

.add-ingredient {
  margin-top: 10px;
  padding: 10px;
  background: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

/* Стили для вкладок */
.tabs-container {
  margin-top: 20px;
}

.tabs {
  display: flex;
  margin-bottom: 15px;
}

.tab-btn {
  flex: 1;
  padding: 10px;
  border: none;
  background: #f0f0f0;
  cursor: pointer;
}

.tab-btn.active {
  background: #ddd;
}

.tab-content {
  display: none;
}

.tab-content.active {
  display: block;
}

/* Модальные окна */
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 20px;
  border-radius: 10px;
}

.modal-content input,
.modal-content select,
.modal-content textarea {
  width: 100%;
  margin-bottom: 10px;
  padding: 5px;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
}

.modal-buttons button {
  margin-left: 10px;
  padding: 5px 10px;
  cursor: pointer;
}

/* Стили для видео галереи */
.video-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 10px;
}

.video-item {
  position: relative;
}

.video-item video {
  width: 100%;
  height: auto;
}

.remove-video {
  position: absolute;
  top: 5px;
  right: 5px;
  background: rgba(255,0,0,0.7);
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
}
.steps-list {
  display: block;
  margin-top: 10px;
}




.header-controls {
  margin-top: 20px;
}

#search-input {
  padding: 10px;
  width: 60%;
  max-width: 300px;
}

#add-recipe-btn {
  padding: 10px 20px;
  background-color: #45a049;
  color: white;
  border: none;
  cursor: pointer;
}

.recipes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
